<template>
  <div>
    <MHeader :back="true">列表</MHeader>
    <!--scrollTop+元素.clientHeight+20 == 元素.scrollHeight-->
    <div class="content" ref="scroll" @scroll="loadMore">
      <ul>
        <router-link v-for="(book,index) in books" :to="{name:'detail',params:{bid:book.bookId}}" :key="index" tag="li">
          <img v-lazy="book.bookCover">
          <div>
            <h4>{{book.bookName}}</h4>
            <p>{{book.bookInfo}}</p>
            <b>{{book.bookPrice}}</b>
            <button @click.stop="remove(book.bookId)">删除</button>
          </div>
        </router-link>
      </ul>
      <div @click="more" class="more">加载更多</div>
    </div>
  </div>
</template>

<script>
  /*1.引入组件 2.注册组件 3.使用组件*/
  import MHeader from '../base/MHeader';
  import {pagination, removeBook} from "../api";

  export default {
    data() {//组件中的data是函数，返回一个对象
      return {
        books: [],
        //offset代表偏移量
        offset: 0,
        //hasMore代表是否更多
        hasMore: true,
        //默认不是正在加载
        isLoading: false
      }
    },
   /* mounted() {
      //下拉刷新
      let scroll = this.$refs.scroll;//获取到要拖拽的元素
      let top = scroll.offsetTop;
      let distance = 0;
      scroll.addEventListener('touchstart', (e) => {
        //滚动条在最顶端时，并且当前盒子在顶端的时候 才继续走
        if(scroll.scrollTop!=0&&scroll.offsetTop!=top) return;
        let start = e.touches[0].pageY;//手指点击的开始
        let move = (e) => {
          let current = e.touches[0].pageY;
          distance = current - start;//求的拉动的距离（负数不要）
          if (distance > 0) {//如果大于50了，就默认为是50像素
            if (distance <= 50) {
              scroll.style.top = distance + top + 'px';
            } else {
              distance = 50;
              scroll.style.top = top + '50px';
            }
          } else {
            //如果不再考虑范围内 移除掉move和end事件
            scroll.removeEventListener('touchmove', move);
            scroll.removeEventListener('touchend', end);
            return
          }
        };
        let end = (e) => {
          clearTimeout(this.timer1);
          this.timer1 = setInterval(() => {//一共 distance   每次-1
            if (distance <= 0) {
              clearTimeout(this.timer1);
              distance = 0;
              scroll.style.top=top+'px';
              scroll.removeEventListener('touchmove', move);
              scroll.removeEventListener('touchend', end);
              this.books=[];//先清空数据
              this.offset=0;
              this.getData();
              return
            }
            distance -= 1;
            scroll.style.top = top + distance + 'px';
          }, 1)
        };
        scroll.addEventListener('touchmove', move);
        scroll.addEventListener('touchend', end);
      }, false);
    },*/
    created() {
      this.getData();
    },
    methods: {
      async getData() {
        if (this.hasMore && !this.isLoading) {//有更多的时候获取数据
          this.isLoading = true;
          let {hasMore, books} = await pagination(this.offset);
          this.books = [...this.books, ...books];//获取的书  放到books属性上
          this.hasMore = hasMore;
          this.isLoading = false;
          this.offset = this.books.length;//维护偏移量的就是总书的长度
        }

      },
      async remove(id) {
        await removeBook(id);//删除某一项
        //要删除前台数据
        this.books = this.books.filter(item => item.bookId !== id);
      },
      more() {
        this.getData();
      },
      loadMore() {
        //进来时触发scroll事件，可能触发N次，先进来开一个定时器，下次触发时将上次的定时器清除掉
        clearTimeout(this.timer);//节流（防抖）
        this.timer = setTimeout(() => {
          //  卷去的高度  当前可见区域  总高
          let {scrollTop, clientHeight, scrollHeight} = this.$refs.scroll;
          if (scrollTop + clientHeight + 20 > scrollHeight) {
            this.getData();//获取更多
          }
        }, 60);

      }

    },
    computed: {},
    components: {
      MHeader
    }
  }
</script>
<style scoped>
  .content ul {
    padding: 10px;
  }

  .content ul li {
    display: flex;
    align-items: center;
    padding-bottom: 10px;
    border-bottom: 1px solid #f1f1f1;
  }

  .content ul li img {
    width: 130px;
    height: 150px;
  }

  .content h4 {
    font-size: 20px;
    line-height: 25px;
  }

  .content p {
    color: #2a2a2a;
    line-height: 25px;
    margin-top: 10px;
  }

  .content b {
    color: red;
    display: block;
    margin-top: 10px;
  }

  .content button {
    width: 60px;
    height: 20px;
    margin-top: 10px;
    background: rebeccapurple;
    color: #fff;
    border: none;
    border-radius: 10px;
    text-align: center;
    line-height: 20px;
    outline: none;
  }

  .more {
    margin: 10px;
    background: #2afedd;
    height: 30px;
    line-height: 30px;
    font-size: 20px;
    text-align: center;
    border-radius: 5px;
  }
</style>



